.FeedbackOptions {
  display: flex;
  justify-content: center;
}

.button:not(:last-child) {
  margin-right: 10px;
}

.button {
  width: 70px;
  height: 25px;
  background-color: #d4c2aac7;
  border: none;
  font-size: 16px;
  font-weight: 600;
  border-radius: 10%;
}

.button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0px 5px 10px 2px rgba(10, 18, 24, 0.2);
  background-color: #975908c7;
}

.button:focus {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0px 5px 10px 2px rgba(10, 18, 24, 0.2);
  background-color: #975908c7;
}

.Section {
  display: inline;
  text-align: center;
}

.title {
  color: antiquewhite;
  font-size: 26px;
  font-weight: 500;
}

.Statistics {
  color: bisque;
}

.notifacation {
  color: rgb(201, 155, 99);
  font-weight: 100;
}

.App {
  width: 300px;
  padding: 20px;

  background-image: linear-gradient(
      rgba(47, 48, 58, 0.4),
      rgba(47, 48, 58, 0.4)
    ),
    url(/static/media/hero.6904241a.jpg);
  background-size: cover;
}

.doyoulike {
  color: antiquewhite;
  font-style: italic;
  font-size: 20px;
}

